* {    margin: 0;    padding: 0;    box-sizing: border-box;}body {    font-family: "Times New Roman", 'serif';    background-color: #cccccc;}.container {    max-width: 1200px;    padding: 0 20px;    margin: 0 auto;}ul {    margin: 0;    list-style: none;}.box-wrapper {    border-radius: 10px;    overflow: hidden;    box-shadow: 0 2px 15px rgba(0,0,0,0.5);}.App {    padding-top: 50px;    padding-bottom: 50px;}